body{
  overflow-x: hidden;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;

}
ul,ol,li{
    list-style: none;
}
a{
    text-decoration: none;
    color: #000;
}
table {
    border-collapse: collapse;
    border: none;
    th, td {
      border: none;
    }
  }
  fieldset {
    border: none;
  }

  .h {
    position: absolute; top: -99999px; opacity: 0;
  }



@keyframes fade{
    from{
        opacity: 0;
        transform: translateY(60px);
    }
    to{
        opacity: 1;
        transform: translateY(0px);
    }
}

